const verifyAccessh=function(requiredRole){
    return async(req,res,next)=>{
    
         try{
            
            const role=req.hospital.role;
            if(requiredRole !==role){
                return res.status(403).json({
                    status:403,
                    message: "you are not authorized ü§∑‚Äç‚ôÄÔ∏è"
                })
            }
            return next();
        }
        catch(err){
            console.log(err);
        }
    }
    
    }
    export default verifyAccessh;